import CenterNavBar from '@/third/acaui/nav/center-nav-bar.vue'
import {
	mapActions,
	mapGetters
} from 'vuex'
export default {
	components: {
		CenterNavBar
	},
	computed: {
		...mapGetters({
			userInfo: 'user/userInfo'
		})
	},
	methods: {
		...mapActions('user', ['setUserInfo', 'setLoginStatus']),
		//退出处理
		loginOut() {
			uni.removeStorageSync('userInfo');
			this.setUserInfo({})
			this.setLoginStatus(false)
			this.$Router.push({
				name: 'login'
			})
		}
	}
}
